<template>
    <div class="container">
        <show-nav dtTitle="猫眼电影" navBackImage="linear-gradient(to right, rgb(250, 60, 104) 2%, rgb(254, 70, 77) 97%"
                  :titleList="titleList" isNavAll="true" @showIndex="changeVideoIndex"></show-nav>
        <template v-for="(item,index) in showVideoList">
              <showImgCarousel :key="index" :showVideoItem="item" v-if="index == curIndex"></showImgCarousel>
        </template>
    </div>
</template>

<script>
import ShowNav from '@/components/RouterView/showimg/ShowNav.vue'
import ShowImgCarousel from '@/components/RouterView/figure/ShowImgCarousel.vue'
export default {
    props:['titleList','showVideoList'],
    components:{
      ShowNav,
      ShowImgCarousel
    },
    data(){
        return {
            curIndex:0
        }
    },
    methods:{
        changeVideoIndex(index){
            console.log(index,'video');
            this.curIndex = index;
        }
    }
}
</script>

<style lang="less">
  @import url('../../../assets/style/HomePage/ShowVideo.less');
</style>



