<template>
    <div class="container">
        <show-nav @showIndex="changeCurIndex" :titleList="titleList"
                  :dtTitle="dtTitle" :navBackImage="navBackImage" :isNavAll="isNavAll"/>
        <template v-for="(item,index) in showImgList">
              <div class="container-showbox"
                 v-show="index == curIndex"
                 :key="index">
                      <div class="showbox-item" :key="dex"
                          v-for="(tem,dex) in showImgList[index]"
                          :style="{width:itemWidth,boxSizing:boxSizing,height:itemHeight,
                                   padding:itemPadding,margin:itemMargin}">
                          <div class="showbox-img" :style="{height:showImgHeight}">
                              <img :src="tem.imgUrl" :alt="dex" :style="{height:imgHeight,width:imgWidth}">
                              <slot name="headimg" :msg="tem.headImg"></slot>
                          </div>
                         <p class="showbox-bigtitle">{{tem.bigTitle}}</p>
                         <p class="showbox-smalltitle">{{tem.smallTitle}}</p>
                         <slot></slot>
                         <p class="showbox-price">
                             <span class="curprice" :style="{color:curColor}">￥{{tem.curPrice}}</span>
                             <span class="havemean" v-if="tem.isHaveMean">/人均</span>
                             <slot :msg="tem.prevPrice" :index="index" name="prevprice"/>
                             <slot :msg="tem.address?tem.address:tem.sell" name="rightspan"/>    
                         </p>
                      </div>
              
                 </div>
        </template>
        
    </div>
</template>

<script>
import ShowNav from '@/components/RouterView/showimg/ShowNav.vue'
export default {
  props:['titleList','showImgList','dtTitle','itemWidth','navBackImage',
         'boxSizing','showImgHeight','itemHeight','itemPadding','itemMargin',
         'imgHeight','imgWidth','curColor','isNavAll'],
   data(){
       return {
           curIndex:0,
       }
   },
   created(){
        
   },
   methods:{
       changeCurIndex(props){
           this.curIndex = props;
       }
   },
   components:{
       ShowNav
   }
}
</script>

<style>

</style>



