<template>
    <div class="container-item">
      <el-carousel  height="" :autoplay="false" class="big-carousel" indicator-position="outside">
          <template v-for="(item,index) in showVideoItem">
              <!-- 两个数组 -->
                 <el-carousel-item :key="index">
                    <div class="carousel-item" v-for="(tem,dex) in item" :key="dex">
                         <img :src="tem.imgUrl" alt="video" class="film-img">
                         <img src="//s0.meituan.net/bs/fe-web-meituan/25e6614/img/imax3d.png" alt="3d"
                              v-if="tem.threeImg" class="three-img">
                         <img src="//s0.meituan.net/bs/fe-web-meituan/25e6614/img/imax.png" alt="3ds"
                              v-if="tem.threeImgSmall" class="three-img">
                         <div class="film-info">
                             <p class="viewer">
                                 <span v-if="tem.viewer" class="msg">观众评</span>
                                 <span class="viewer-score" v-if="tem.viewer">{{tem.viewer}}</span>
                                 <span v-if="tem.wantSee" class="viewer-score">{{tem.wantSee}}</span>
                                 <span v-if="tem.wantSee" class="msg">人想看</span>
                             </p>
                             <p class="film-name-box">
                                 <span class="film-name">{{tem.filmName}}</span>
                                 <span class="buy-click" v-if="!tem.buyClick&&!tem.noBuy">购票</span>
                                 <span class="buy-click" v-else-if="!tem.noBuy">{{tem.buyClick}}</span>
                             </p>
                         </div>
                    </div>
                 </el-carousel-item>
          </template>
      </el-carousel>
    </div>
</template>

<script>
export default {
  props:['showVideoItem']
}
</script>

<style lang="less">
  
</style>



