<template>
    <div class="container-nav" :style="{backgroundImage:navBackImage}">
            <dl>
                <dt>{{dtTitle}}</dt>
                <dd v-for="(item,index) in titleList" :key="index"
                    @mouseenter.self="myEnter(index)"
                    :class="{'active':isHoverList[index]}">
                    {{item}}
                </dd>
                <dd class="navAll" v-if="isNavAll">全部 ></dd>
                <slot name="navAll"></slot>
            </dl>
        </div>
</template>

<script>
export default {
   props:['navBackImage','dtTitle','titleList','isNavAll'],
   data(){
      return {
          isHoverList:[],
      }
  },
  created(){
       this.isHoverList[0] = true;
   },
   methods:{
       myEnter(index){
           this.isHoverList.splice(0);
           this.isHoverList[index] = true;
           this.$emit('showIndex',index);
       }
   },
}
</script>

<style>

</style>



